import { Component } from '@angular/core';

import { NavController, NavParams } from 'ionic-angular';


@Component({
  selector: 'page-contatos',
  templateUrl: 'contatos.html'
})
export class ContatosPage {
  icons: string[];
  items: Array<{title: string, note: string, icon: string}>;
  notes: string[];
  titles: string[];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.titles = ['Carlos', 'Roberta',  'Rafaela',  'João',
      'Ana', 'Felipe', 'Vitor', 'Maria', 'Igor', 'Pedro'];
    this.icons = ['flask', 'wifi', 'beer', 'football', 'basketball', 'paper-plane',
    'american-football', 'boat', 'bluetooth', 'build'];
    this.notes = ['claro q v...', 'com ctz! e...', 'vc toca be...', 'só bateria...', 'n posso hj...', 'tbm curto ...',
      'Novo Match', 'Novo Match', 'Novo Match', 'Novo Match'];

    this.items = [];
    for(let i = 1; i < 11; i++) {
      this.items.push({
        title: this.titles[Math.floor(Math.random() * this.notes.length)],
        note: this.notes[i-1],
        icon: this.icons[Math.floor(Math.random() * this.icons.length)]
      });
    }
  }

  /*itemTapped(event, item) {    futuramente abrir chat
    this.navCtrl.push(ItemDetailsPage, {
      item: item
    });
  }*/
}



